import React, { lazy, Suspense } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import Loading from 'components/shared-components/Loading';
import { APP_PREFIX_PATH } from 'configs/AppConfig'

export const AppViews = () => {
  return (
    <Suspense fallback={<Loading cover="content"/>}>
      <Switch>
        <Route path={`${APP_PREFIX_PATH}/home`} component={lazy(() => import(`./home`))} />
        <Route path={`${APP_PREFIX_PATH}/document-single/:id`} component={lazy(() => import(`./document-single`))} />
        <Route path={`${APP_PREFIX_PATH}/document`} component={lazy(() => import(`./document`))} />
        <Route path={`${APP_PREFIX_PATH}/transactions`} component={lazy(() => import(`./transactions`))} />
        <Route path={`${APP_PREFIX_PATH}/profile`} component={lazy(() => import(`./profile`))} />
        <Redirect from={`${APP_PREFIX_PATH}`} to={`${APP_PREFIX_PATH}/home`} />
      </Switch> 
    </Suspense>
  ) 
}
 
export default React.memo(AppViews); 